.alc-pagination {
    @apply flex;
}

.alc-pagination__content {
    @apply flex list-none items-center;
}

.alc-pagination__button--first,
.alc-pagination__button--last {
    @apply hidden sm:block;
}

.alc-pagination__item {
    @apply flex items-center mx-0.5;
}

.alc-pagination__select {
    --border-width: theme('borderWidth.DEFAULT');

    @apply mx-1 py-1;
    border-width: var(--border-width);
    padding-left: calc(theme('padding.1') - var(--border-width));
    padding-right: calc(theme('padding.1') - var(--border-width));
    max-height: 2rem;
    min-width: 4rem;
}
