
alc-pagination {
    @apply block;
}
.alc-pagination {
    @apply flex px-0 list-none;
}

.alc-pagination__link {
    @apply mx-1 px-2 border border-solid border-transparent;
}

.alc-pagination__link.is-active {
    @apply cursor-not-allowed no-underline text-current border-current;
}

.alc-pagination__link.is-disabled {
    @apply cursor-not-allowed no-underline text-current;
}